import React from "react";
import { isAndroid, isIOS } from 'react-device-detect';
import { MxButton } from "views/mobileView/components/atoms/MxButton";

export default function QRCode(props) {


    const redirectLinks = () => {
        if (isAndroid) {
            window.location.href =
                "https://play.app.goo.gl/?link=https://play.google.com/store/apps/details?id=com.investments.mintbox";
        } else if (isIOS) {
            window.location.href =
                "https://apps.apple.com/in/app/mintbox-investment-portfolios/id1628540566";
        } else {
            window.location.href =
                "https://mintbox.ai/";
        }
        setTimeout(() => {
            window.close();
        }, 2000)
    }

    React.useEffect(() => {
        redirectLinks()
    });


    return (
        <div
            style={{
                display: "flex",
                flexDirection: "column",
                minHeight: "90vh",
                alignItems: "center",
                justifyContent: "center",
            }}
        >
            <MxButton
                onPress={() => redirectLinks()}
                name="Download the app"
                style={{
                    borderColor: "#F7D749",
                    backgroundColor: "#F7D749",
                    marginTop: 0,
                }}
            />
        </div>
    );
}
